import {GatsbyImage} from "gatsby-plugin-image"
import {FC, useState} from "react"
import Fader from "~/components/UI/Fader/Fader"
import Blank from "../../Sections/Blank/Blank"
import DotsSelect from "../DotsSelect/DotsSelect"
import RichTextField from "../RichText/RichText"
import "./ImageSection.css"

type ImageSectionProps = {
    readonly slides:
        | ReadonlyArray<{
              readonly title: string | null
              readonly description: {readonly raw: string | null} | null
              readonly image: {
                  readonly title: string | null
                  readonly height: number | null
                  readonly width: number | null
                  readonly url: string | null
                  readonly gatsbyImageData: import("gatsby-plugin-image").IGatsbyImageData | null
              } | null
          } | null>
        | null
        | undefined
}

const ImageSection: FC<ImageSectionProps> = props => {
    const {slides} = props
    const [selected, setSelected] = useState(0)

    return (
        <div className="image-section-hj78">
            <div className="flex desktop-jh87">
                {slides?.map((slide, index) => {
                    return (
                        <div key={index}>
                            {slide?.image?.gatsbyImageData && (
                                <GatsbyImage
                                    image={slide.image.gatsbyImageData}
                                    alt={slide.image.title ?? ""}
                                    className="image-jh87"
                                />
                            )}
                            <Blank>
                                <RichTextField className="description-t99q">{slide?.description?.raw}</RichTextField>
                            </Blank>
                        </div>
                    )
                })}
            </div>

            <div className="mobile-jh87">
                <Fader selected={selected} onSelect={setSelected} slideClass="slide-t99q image-section-hj78">
                    {slides?.map((slide, index) => (
                        <Fader.Slide key={index}>
                            {slide?.image?.gatsbyImageData && (
                                <GatsbyImage
                                    image={slide.image.gatsbyImageData}
                                    alt={slide.image.title ?? ""}
                                    className="image-jh87"
                                />
                            )}

                            <Blank>
                                <RichTextField className="description-t99q">{slide?.description?.raw}</RichTextField>
                            </Blank>
                        </Fader.Slide>
                    ))}
                </Fader>
                <DotsSelect length={slides?.length} selected={selected} onSelect={setSelected} className="dots-t99q" />
            </div>
        </div>
    )
}

export default ImageSection
